
@at-root .navbar {
  position: relative;
  width: 100%;
  padding: 10px 5%;
  line-height: 1;
  background: linear-gradient(to bottom, rgba(0,0,0,.5), transparent);
  color: white;

  &--fixed-top {
	  position: fixed;
	  top: 0;
	  z-index: 20; }

  &__menu {

    &--left {
      float: left; }

    &--right {
      float: right; }

    &__item {
      color: white;
      padding: 5px 10px;
      font-weight: normal;
      font-size: 1rem;
      position: relative;

      &:hover {
        color: black; }

      &--toggle {
        display: none; } } } }



@at-root .mobile-menu-container {
  visibility: hidden;
  position: fixed;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  background: rgba(0,0,0,.3);
  z-index: 2;
  padding-top: 10px;

  &--active {
	  visibility: visible; }

		.mobile-menu {
		  transform: translateX(0); } }



@at-root .mobile-menu {
  overflow: auto;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background: #222;
  text-align: center;
  padding-bottom: 20px;
  transform: translateX(-100%);
  transition: transform ease-in-out .15s;

  &__item {
	  padding: 10px 50px;
	  font-size: 1.2rem;
	  border-bottom: 1px solid rgba(0,0,0,.2);
	  color: whitesmoke; }

		&:hover {
		  color: black; } }



@media all and (max-width: 700px) {
  .navbar {
    padding-left: 5px;
    padding-right: 5px; }

  .navbar__menu__item--toggle {
    display: inline-block; }


  .navbar__menu__item--toggle-items {
    display: none; } }



@at-root .logo {
  display: inline-block;
  margin: 5px 10px;
  font-size: 16px;
  font-weight: bold;
  color: white;
  border: 0.25em solid white;
  border-radius: calc(1em / 2);
  padding: 0.5em 1em; }
