.search {
  background: white;
  box-shadow: 5px 0 20px 2px rgba(0,0,0,.1);
  height: 50px;
  line-height: 50px;
  border-radius: 5px;
  padding: 0px 10px;
  font-size: 0;
  color: dimgray;
  margin: 0px 10px;

  @include modifier(blogs) {
    max-width: 940px;
    margin: 50px auto 10px;

    @media all and (max-width: 970px) {
        margin: 50px 10px 10px; } }

  @include element(icon) {
    padding: 0;
    display: inline-block;
    width: 30px;
    height: 100%;
    text-align: center;
    font-size: 16px; }

  @include element(input) {
    font-size: 16px;
    display: inline-block;
    width: calc(100% - 30px);
    height: 100%;
    background: transparent;
    border: none;
    box-shadow: none;
    outline: none;
    color: dimgray; } }
