
.card {
  display: inline-block;
  background: white;
  border-radius: 5px;
  box-shadow: 5px 0 20px 2px rgba(0,0,0,.1);


  @include modifier(blog) {
    width: 300px;
    overflow: hidden;
    margin: 20px 10px;
    vertical-align: top;
    font-size: 1rem;

    .blog__info {
      color: gray;
      font-size: 12px; }

    @media all and (max-width: 660px) {
      display: block;
      margin: 10px auto; } }

  @include element(image) {
    width: 100%;
    max-width: 100%;
    height: auto;
    overflow: hidden; }

  @include element(title) {
    padding: 10px;
    color: dimgray;
    font-size: 1.5rem;
    font-weight: bolder;
    border-bottom: 1px solid rgba(0, 0, 0, .1); }

  @include element(body) {
    padding: 10px; }

  @include element(footer) {
    font-size: 1rem;
    border-top: 1px solid rgba(0,0,0,.1);
    padding: 5px 0; } }
